.works-container {
    display: flex;
    flex-direction: column;
    align-items: left;
    width: 100%;
    height: 100%;
    padding: 32px 10px;
}

.works-container h1 {
    align-items: center;
}

.works-item img {
    /* border: 5px solid #111e38; */
    border-bottom: 10px solid #fcd212;
    border-left: 10px solid #003087;

    /* border-radius: 5%; */
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.works-item:hover {
    /* border: 1px solid #333; */
    transform: scale(1.1);
    transition: all 0.3s ease;
    cursor: pointer;
}

.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.about span {
    font-size: 1.5rem;
    font-weight: 600;
    color: #111e38;

}

.about p i {
    margin: 0 5px;
    color: #c31f0c;
}

/* media queries */
@media(min-width:768px) {
    .works-list-container {

        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
    }

    .works-item {
        width: 30%;
        margin: 10px;
    }
}